import React, { Component } from 'react';
import updateHandle from './index'
import {Added} from './AddedData'

class Arabic extends Component {

    state = {phone: false, grams: 100}

    gramHandle = (event) => {
        if (event.target.value < 1) {
            this.setState({grams: event.target.min});
        } else if (event.target.value > 10000) {
            this.setState({grams: event.target.max});
        } else {
            this.setState({grams: event.target.value});
        }
    }

    calc = x => ((x * this.state.grams) / 100).toFixed(1);
    calcKcal = x => ((x * this.state.grams) / 100).toFixed(0);

    static getDerivedStateFromProps(props, state) {
        let isPhone = window.innerWidth < 600? true : false;
        return {phone: isPhone };
    }

    PhoneViewArabic() {
        return(
            <div className='cardPhone' style={{backgroundImage: this.props.img }}>

                <button 
                    id='addButtonPc'
                    className='material-icons'
                    onClick={() => {
                                Added.push({name: this.props.name, gram: this.state.grams, cal: this.calcKcal(this.props.cal)})
                                updateHandle();
                                document.getElementById('sumPhone').style.display='inline-flex';
                                window.scrollTo(0,0)
                            }}>add
                </button>

                <div className="kcalDetailsPhoneAr"><h3>
                    <p>{this.calc(this.props.fat)} غ</p> دهون:
                    <br></br>سكريات:<p>{this.calc(this.props.carb)} غ</p>
                    <br></br>ألياف:<p>{this.calc(this.props.fiber)} غ</p>
                    <br></br>سكر:<p>{this.calc(this.props.sugar)} غ</p>
                    <br></br>بروتين:<p>{this.calc(this.props.protein)} غ</p>
                    </h3>
                </div>

                <div className='titlePhoneAr'>
                    <h2>{this.props.name}</h2><br></br>
                    <h5>{this.calcKcal(this.props.cal)} سعرة حرارية</h5><br></br>
                    <h6>كل <span>{this.state.grams}</span><input id="gram" type="number" placeholder={this.state.grams} min='1' max='10000' onChange={this.gramHandle} /> غرام</h6>
                </div>

            </div>
        )
    }

    PcViewArabic() {
        return(
            <div className='cardPc' style={{backgroundImage: this.props.img }}>

                <button 
                    id='addButtonPc'
                    className='material-icons'
                    onClick={() => {
                                Added.push({name: this.props.name, gram: this.state.grams, cal: this.calcKcal(this.props.cal)})
                                updateHandle();
                                var y = Added.map(o => Number(o.cal));
                                if (y.length !== 0) {document.getElementById('cardsContainer').style.marginLeft='160px'}
                                document.getElementById('sumPc').style.display = 'block'
                                }}>add
                </button>
                
                <div className="kcalDetailsPcAr"><h3>
                    دهون:<p>{this.calc(this.props.fat)} غ</p> 
                    <br></br>سكريات:<p>{this.calc(this.props.carb)} غ</p>
                    <br></br>ألياف:<p>{this.calc(this.props.fiber)} غ</p>
                    <br></br>سكر:<p>{this.calc(this.props.sugar)} غ</p>
                    <br></br>بروتين:<p>{this.calc(this.props.protein)} غ</p>
                    </h3>
                </div>

                <div className='titlePcAr'>
                    <h4>{this.props.name}</h4>
                    <h5>{this.calcKcal(this.props.cal)} سعرة حرارية</h5>
                    <h6>كل <span>{this.state.grams}</span><input id="gram" type="number" placeholder={this.state.grams} min='1' max='10000' onChange={this.gramHandle} /> غرام</h6>
                </div>

            </div>
        )
    }

    render() {        
        if(this.state.phone) {return this.PhoneViewArabic()} else {return this.PcViewArabic()}
    }

}

export default Arabic