import React, { Component } from 'react';
import updateHandle from './index'
import {isAr} from './index'

let Added = []

class AddedFruits extends Component {

    detailRender = () => {
        if (isAr) {
            return this.props.cal + ' سعرة • ' + this.props.gram + 'غ '
        } else {
            return this.props.cal + ' kcal •  ' + this.props.gram + ' g' 
        }
    }
    componentDidMount() {
        if (window.innerWidth < 600) {
        document.getElementById('itemsContainerPc').id='itemsContainerPhone'
        }
    }

    render () {
        return (
            <div id='itemsContainerPc'>
                <p className='itemNamePc'>{this.props.name}</p>

                <button 
                    id='deleteButtonPc'
                    className='material-icons'
                    onClick={() => {
                                var x= Added.findIndex((e) => e.name === this.props.name);
                                Added.splice(x, 1);
                                updateHandle();
                                var y = Added.map(o => Number(o.cal));
                                if (y.length === 0 && window.innerWidth > 600) {document.getElementById('sumPc').style.display = 'none';document.getElementById('cardsContainer').style.margin='0'}
                                if (y.length === 0 && window.innerWidth < 600) {document.getElementById('sumPhone').style.display='none'}
                    }}>clear
                </button>

                <p id="itemDetailsPc">{this.detailRender()}</p>
            </div>
        )
    }
}
export default AddedFruits;
export {Added}

